.widget-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.widget-toolbar {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 500;

  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }
}

.widget-content {
  flex: 1;
  padding: 16px;
  overflow-y: auto;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  color: #9e9e9e;

  mat-icon {
    font-size: 48px;
    width: 48px;
    height: 48px;
    margin-bottom: 16px;
  }

  p {
    margin: 0;
    font-size: 14px;
  }
}

.stats-summary {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  margin-bottom: 24px;

  .stat-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px;
    background: linear-gradient(135deg, rgba(79, 172, 254, 0.1) 0%, rgba(0, 242, 254, 0.1) 100%);
    border-radius: 8px;

    mat-icon {
      font-size: 32px;
      width: 32px;
      height: 32px;
      color: #4facfe;
    }

    .stat-info {
      display: flex;
      flex-direction: column;
      gap: 4px;

      .stat-label {
        font-size: 12px;
        color: var(--text-secondary);
      }

      .stat-value {
        font-size: 20px;
        font-weight: 600;
        color: var(--text-primary);
      }
    }
  }
}

.recent-novels {
  h3 {
    font-size: 14px;
    font-weight: 500;
    color: var(--text-secondary);
    margin: 0 0 12px 0;
  }

  .novel-list {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .novel-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px;
    background: rgba(79, 172, 254, 0.05);
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s ease;

    &:hover {
      background: rgba(79, 172, 254, 0.1);
      transform: translateX(4px);
    }

    > mat-icon:first-child {
      font-size: 24px;
      width: 24px;
      height: 24px;
      color: #4facfe;
    }

    .novel-info {
      flex: 1;
      min-width: 0;

      .novel-title {
        font-size: 14px;
        font-weight: 500;
        color: var(--text-primary);
        margin-bottom: 4px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .novel-characters {
        font-size: 12px;
        color: var(--text-secondary);
      }
    }

    .arrow-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
      color: var(--text-secondary);
    }
  }
}

@media (max-width: 768px) {
  .stats-summary {
    grid-template-columns: 1fr;
  }
}
